/**
 *
 * Alignment
 *
 * @author Takuto Yanagida
 * @version 2021-11-17
 *
 */


window['NACSS'] = window['NACSS'] || {};


(function (NS) {

	(function () {
		/**
		 *
		 * Float
		 *
		 * @author Takuto Yanagida
		 * @version 2021-11-17
		 *
		 */
		
		
		function initialize(tars = {}, opts = {}) {
			tars = Object.assign({ left: [], right: [] }, tars);
			if (tars['left'].length + tars['right'].length === 0 ) return;
		
			opts = Object.assign({
				styleAlign : ':ncAlign',
				classLeft  : 'alignleft',
				classRight : 'alignright',
				classCenter: 'aligncenter',
				minWidth   : 240,  // px
			}, opts);
		
			setTimeout(() => {
				assignEventListener(tars['left'], opts['classLeft'], opts);
				assignEventListener(tars['right'], opts['classRight'], opts);
			}, 0);  // Delay
		}
		
		function assignEventListener(as, cls, opts) {
			const aws = as.map(e => [e, 0]);
			onScroll(() => storeWidth(aws, cls, opts), true);  // For lazy loading
			onResize(() => update(aws, cls, opts), true);
		}
		
		
		// -------------------------------------------------------------------------
		
		
		function storeWidth(aws, style, opts) {
			for (const aw of aws) {
				const [a, w] = aw;
				if (10 < w) continue;
				switchFloatOne(a, false, style, opts);
				aw[1] = a.getBoundingClientRect().width;
				switchFloatOne(a, true, style, opts);
			}
		}
		
		function update(aws, cls, opts) {
			for (const aw of aws) {
				const [a, w] = aw;
				const cw = contentWidth(a.parentElement);
				const wn = a.getBoundingClientRect().width;
				if (wn < cw * 0.9 && w < wn) {
					aw[1] = wn;
				}
			}
			for (const [a, w] of aws) {
				const cw = contentWidth(a.parentElement, true);
				switchFloatOne(a, cw - w < opts.minWidth, cls, opts);
			}
		}
		
		function switchFloatOne(a, enabled, origCls, opts) {
			if (enabled && !hasClass(a, opts.styleAlign)) {
				setClass(a, opts.styleAlign, true, origCls);
				a.classList.remove(origCls);
				a.classList.add(opts.classCenter);
			}
			if (!enabled && hasClass(a, opts.styleAlign)) {
				a.classList.remove(opts.classCenter);
				a.classList.add(origCls);
				setClass(a, opts.styleAlign, false);
			}
		}
		
		function contentWidth(e, checkDisplay = false) {
			if (e === null) {
				return window.innerWidth;
			}
			const s = getComputedStyle(e);
			if (checkDisplay && s.display === 'inline') {
				return contentWidth(e.parentElement);
			}
			const ph = parseFloat(s.paddingLeft) + parseFloat(s.paddingRight);
			return e.clientWidth - ph;
		}
		
		NS.alignmentFloat = initialize;
	})();

	(function () {
		/**
		 *
		 * Selector Attributes
		 *
		 * @author Takuto Yanagida
		 * @version 2021-11-17
		 *
		 */
		
		
		function initialize(ts, opts = {}) {
			if (ts.length === 0) return;
		
			opts = Object.assign({
				styleHasOnlyChild: ':ncHasOnlyChild',
				stylePrefixNextTo: ':ncNextTo',
			}, opts);
		
			addHasOnlyChild(ts, opts['styleHasOnlyChild']);
			addNextTo(ts, opts['stylePrefixNextTo']);
		}
		
		function addHasOnlyChild(es, styleHasOnlyChild) {
			for (const e of es) {
				if (hasOnlyChildOne(e)) {
					setClass(e.parentElement, styleHasOnlyChild);
				}
			}
		}
		
		function hasOnlyChildOne(e) {
			for (const c of Array.from(e.parentElement.childNodes)) {
				if (
					(c.nodeType === 1 /*ELEMENT_NODE*/ && c !== e) ||
					(c.nodeType === 3 /*TEXT_NODE*/ && c.textContent.trim() !== '')
				) {
					return false;
				}
			}
			return true;
		}
		
		function addNextTo(es, stylePrefixNextTo) {
			for (const e of es) {
				const prev = e.previousElementSibling;
				if (prev && prev.className === '') {
					const tn = prev.tagName.toLowerCase();
					const cc = tn.charAt(0).toUpperCase() + tn.slice(1);
					setClass(e, stylePrefixNextTo + cc);
				}
			}
		}
		
		NS.alignmentSelectorAttribute = initialize;
	})();

	/**
	 *
	 * Style Class Utilities
	 *
	 * @author Takuto Yanagida
	 * @version 2021-11-11
	 *
	 */
	
	
	function hasClass(tar, cls) {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			return tar.dataset[key] !== undefined;
		} else {
			return tar.classList.contains(key);
		}
	}
	
	function setClass(tar, cls, enabled = true, val = '') {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			if (enabled) {
				tar.dataset[key] = val;
			} else {
				delete tar.dataset[key];
			}
		} else {
			const c = [key, val].join('-');
			if (enabled) {
				tar.classList.add(c);
			} else {
				tar.classList.remove(c);
			}
		}
	}
	
	function getSelector(cls) {
		if (cls.startsWith(':')) {
			return `*[data-${cls.substr(1).replace(/([A-Z])/g, c => '-' + c.charAt(0).toLowerCase())}]`;
		} else {
			return `*${cls}`;
		}
	}
	
	/**
	 *
	 * Utilities
	 *
	 * @author Takuto Yanagida
	 * @version 2021-11-11
	 *
	 */
	
	
	function getScrollOffset() {
		const s = getComputedStyle(document.documentElement);
		return parseInt(s.scrollPaddingTop);
	}
	
	
	// -----------------------------------------------------------------------------
	
	
	function throttle(fn) {
		let isRunning;
		return (...args) => {
			if (isRunning) return;
			isRunning = true;
			requestAnimationFrame(() => {
				isRunning = false;
				fn(...args);
			});
		};
	}
	
	const resizeListeners = [];
	const scrollListeners = [];
	
	function onResize(fn, doFirst = false) {
		if (doFirst) fn();
		resizeListeners.push(throttle(fn));
	}
	
	function onScroll(fn, doFirst = false) {
		if (doFirst) fn();
		scrollListeners.push(throttle(fn));
	}
	
	document.addEventListener('DOMContentLoaded', () => {
		window.addEventListener('resize', () => {
			for (const l of resizeListeners) l();
		}, { passive: true });
		window.addEventListener('scroll', () => {
			for (const l of scrollListeners) l();
		}, { passive: true });
	});
	
	
	// -----------------------------------------------------------------------------
	
	
	function onIntersect(fn, targets, threshold) {
		const vs = Array(targets.length).fill(false);
		const tm = new Map();
		targets.forEach((t, i) => tm.set(t, i));
	
		function init() {
			const io = new IntersectionObserver((es) => {
				for (const e of es) vs[tm.get(e.target)] = e.isIntersecting;
				fn(vs);
			}, { rootMargin: `${mt}px 0px 0px 0px`, threshold });
			for (const t of targets) io.observe(t);
			return io;
		}
		let mt = 0;
		let io = null;
		let st = null;
		onResize(() => {
			mt = -getScrollOffset();
			if (st) clearTimeout(st);
			st = setTimeout(() => {
				if (io) io.disconnect();
				io = init();
			}, 100);
		}, true);
	}
	

})(window['NACSS']);
